@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
  transition: 0.4s ease-in-out;
}
.navbar {
  font-size: 14px;
  font-family: "Nunito";
  text-transform: uppercase;
  padding-top: 25px;
  letter-spacing: 1px;
  height: 80px;
  background-color: #f8f8f8;
}
.navbar-collapse {
  background-color: #f8f8f8;
}
.navbar .nav-link {
  font-weight: 600;
  color: #95c100;
  text-transform: capitalize;
  margin-right: 30px;
  margin-top: 10px;
}
.navbar .nav-link a {
  color: #95c100;
}
.logo {
  width: 147px;
}
.navbar .search-box {
  color: #222222;
  font-family: "Nunito";
  width: 350px;
  top: 20px;
  display: block;
  margin: 16px auto;
  text-align: center;
}
.search-box .header {
  font-size: 14px;
  padding: 10px;
  margin-top: -20px;
  font-weight: 600;
  border: none;
  border-radius: 0%;
  border-bottom: 1px solid #e8e8e8;
}
.deliver-section {
  text-align: left;
  font-size: 14px;
  padding: 10px;
  padding-bottom: 13px;
  border-bottom: 1px solid #e8e8e8;
  text-transform: capitalize;
  font-weight: 500px;
}
.deliver-section span {
  color: #95c100;
}
.navbar .nav-bar-icon {
  font-size: 20px;
  color: #95c100;
  margin-right: 10px;
}

.search-box .location-selection p {
  color: #bebebe;
  font-size: 14px;
  margin-top: 20px;
  font-weight: 600;
  text-transform: capitalize;
}
.search-box .location-selection h1 {
  font-size: 15px;
  text-transform: capitalize;
  color: #fe9000;
}
.search-box .location-selection .city_tag {
  font-size: 10px;
  background-color: #f4f5f5;
  border: 1px solid #ddd;
  border-radius: 50px;
  padding: 4px 10px;
  margin: 10px 4px;
  display: inline-block;
  cursor: pointer;
  color: #868282;
  align-items: center;
}
.search-box .location-search-box {
  margin: 10px auto;
  position: relative;
}
.search-box .location-search-box .ion-ios-search {
  position: absolute;
  top: 13px;
  color: #b6bed4;
  font-size: 20px;
  left: 25px;
}
.search-box .location-search-box .search-input {
  background-color: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 73px;
  padding: 10px 82px 11px 37px;
  width: 90%;
}
.search-box .location-search-box .search-input:focus {
  outline: none;
}
.search-box .location-search-box .search-input::-webkit-input-placeholder {
  color: #6e7faa;
}
.search-box .location-search-box .search-input:-ms-input-placeholder {
  color: #6e7faa;
}
.search-box .location-search-box .search-input::placeholder {
  color: #6e7faa;
}
.search-box .location-search-box .auto-detect-btn {
  position: absolute;
  top: 10px;
  right: 30px;
  border: none;
  color: #6e7faa;
  border-left: 1px solid #ddd;
  padding-left: 5px;
  background-color: #fff;
}
.user-btn {
  background-color: #95c100;
  border-radius: 50px;
  color: white;
  text-align: center;
  padding-left: 30px;
  padding-right: 30px;
}

* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
.cart-content {
  position: absolute;
  top: 0;
  right: 0;
  background: blue;
  width: 100%;
  height: 100%;
  z-index: 3;
  transition: 10s ease-in-out;
  background: rgba(0, 0, 0, 0.5);
}
.cart-box {
  position: absolute;
  background: white;
  width: 320px;
  height: 100%;
  right: 0;
  font-family: "Nunito";
  transition: 0.2s ease-in-out;
}
.cart-header {
  border-bottom: 1px solid #e8e5e5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  width: 90%;
  margin: auto;
}
.cart-header p {
  position: relative;
  margin-top: 20px;
  font-size: 18px;
}
.cart-item {
  color: #95c100;
}
.cart-content .cart-list-scroll {
  height: 50vh;
  overflow: auto;
  overflow-x: hidden;
}
.cart-content .cart-list-box {
  border: 1px solid #eee;
  padding: 5px;
  border-bottom: none;
  display: flex;
  border-bottom: 0.5px solid #e3e3e3;

  width: 98%;
  margin: auto;
}
.cart-content .cart-list-box .cart-img {
  display: inline-block;
  height: 68px;
  margin-right: 4px;
  vertical-align: top;
  width: 68px;
}
.cart-content .cart-details {
  font-size: 12px;
  width: 100%;
}
.cart-content .name-delete {
  display: flex;
  justify-content: space-between;
}
.cart-content .delete-icon {
  color: red;
}
.cart-content .product-name {
  color: #333;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  margin-top: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.cart-content .product-items {
  display: flex;
  justify-content: space-between;
}
.cart-content .button {
  background: none;
  border: none;
  font-size: 15px;
  outline: none;
  margin-right: 10px;
  margin-left: 10px;
  color: #fe9000;
}
.cart-content .total-price {
  margin: 2px;
  color: #95c100;
  font-weight: 600;
}
.cart-content .summary {
  width: 95%;
  margin: auto;
}
.cart-content .card_summary {
  padding: 10px;
  text-align: center;
  color: #333;
  border-top: 0.5px solid #e3e3e3;
  border-bottom: 0.5px solid #e3e3e3;
}
.cart-content .card_price {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}
.cart-content .tex_amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #444444;
  margin-top: -10px;
}

.cart-content .total_amount {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
  padding-top: 10px;
}

.cart-content .summary .btn {
  background: #95c100;
  color: white;
  width: 100%;
  border-radius: 0;
  letter-spacing: 5px;
  margin-bottom: 10px;
}
.cart-content .quantity {
  width: 10%;
  text-align: center;
}

