.footer {
  margin-top: 40px;
  background-color: #f4f4f4;
  border-top: 1px solid #e8e5e5;
  padding: 15px 0px;
  height: 60px;
  margin: auto;
}
.footer .footer-link {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-left: -40px;
}

.footer .fotter-link-text {
  font-family: "Nunito";
  color: #646464;
  margin-right: 34px;
  font-size: 14px;
